	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Don't Keep Knocking","date":"19 Jul 2006","slug":"/2006-07-19-dont-keep-knocking/","tags":"Y2006"},"html":"<p>At <a href=\"http://blogg.de/\">blogg.de</a> we used to run a ping service using XML-RPC (similar to weblogs.com) for German (language or related) weblogs. We <a href=\"http://blogworkorange.de/eintrag.php?id=87\">discontinued the service</a> back in November. The data coming in was at least 99.9% garbage, either spam or from logs having nothing to do with German weblogs. The start page for blogg.de now lists entries only from blogg.de weblogs instead of the entire German blogosphere.</p>\n<p>Now, 8 months later, we still receive over 60000 pings per hour, every hour. I recently set up <a href=\"http://www.modsecurity.org/\">mod&#95;security</a> to filter out the ping requests (with HTTP status \"410 Gone\") while letting through the (very, very) few legitimate requests from registered blogg.de users. The load on our web servers decreased by 30%.</p>\n<p>The number of pings coming in hasn't decreased at all. After 10 days we're still at over 60000 per hour.</p>"},"next":{"fields":{"title":"Poor Role Model","date":"18 Jul 2006","slug":"/2006-07-18-poor-role-model/"}},"previous":{"fields":{"title":"Through the Window","date":"20 Jul 2006","slug":"/2006-07-20-through-the-window/"}}},{"node":{"fields":{"title":"Poor Role Model","date":"18 Jul 2006","slug":"/2006-07-18-poor-role-model/","tags":"Y2006"},"html":"<p><a href=\"http://www.amazon.com/gp/product/0836218256\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2006/07/drooling.jpeg\" alt=\"drooling\" /></a></p>\n<p>When Christopher was born, his uncle gave him a framed print of <a href=\"http://en.wikipedia.org/wiki/Calvin_and_Hobbes\">Calvin and Hobbes</a> for his bedroom.</p>\n<p>This week Christopher discovered a <a href=\"http://www.amazon.com/gp/product/0836218256\">book</a> to go along with with his print.</p>\n<p>I think he's just found a very poor role model. I suspect that was his uncle's plan all along.</p>"},"next":{"fields":{"title":"And Action!","date":"17 Jul 2006","slug":"/2006-07-17-and-action/"}},"previous":{"fields":{"title":"Don't Keep Knocking","date":"19 Jul 2006","slug":"/2006-07-19-dont-keep-knocking/"}}},{"node":{"fields":{"title":"And Action!","date":"17 Jul 2006","slug":"/2006-07-17-and-action/","tags":"Y2006"},"html":"<p><object width=\"425\" height=\"350\"><param name=\"movie\" value=\"http://www.youtube.com/v/hsB7CvfuWuI\" /><embed src=\"http://www.youtube.com/v/hsB7CvfuWuI\" type=\"application/x-shockwave-flash\" width=\"425\" height=\"350\"></embed></object></p>\n<p>As promised, here's Christopher doing laps around the backyard. There's only one firey crash, at 0:22. We're still working on braking and coming to a safe stop.</p>\n<p>Yes, the bike is too small for him. It's two years old, and he's, well, grown in the meantime.</p>"},"next":{"fields":{"title":"Bravery and Balance","date":"16 Jul 2006","slug":"/2006-07-16-bravery-and-balance/"}},"previous":{"fields":{"title":"Poor Role Model","date":"18 Jul 2006","slug":"/2006-07-18-poor-role-model/"}}}],"pathPrefix":"","first":false,"last":false,"index":206,"pageCount":1101}}

